
import { PageHeader } from "@/components/page-header";
import { DriverAppClient } from "@/components/driver-app-client";
import { drivers, trips } from "@/lib/data";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";

export default function HeavyDriverAppPage() {
  // We'll simulate the view for a heavy driver with an active equipment trip
  const driver = drivers.find(d => d.id === 'driver-009');
  const trip = trips.find(t => t.driverId === driver?.id && t.status !== 'COMPLETED' && t.status !== 'CANCELLED');

  if (!driver || !trip) {
    return (
        <>
            <PageHeader
                title="Heavy Driver App"
                description="This is your personal dashboard to manage your jobs and vehicle."
            />
            <Alert>
                <AlertTitle>No Active Job</AlertTitle>
                <AlertDescription>
                    Could not find an active heavy equipment job for the simulation.
                </AlertDescription>
            </Alert>
        </>
    );
  }

  return (
    <>
      <PageHeader
        title={`Welcome, ${driver.user.name}`}
        description="This is your personal dashboard to manage your heavy equipment jobs."
      />
      <DriverAppClient driver={driver} trip={trip} />
    </>
  );
}
